<template>
  <div class="tabs " >
    <div class="tab-item" @click="setCur(0,'deal')" :class="{active: claimTabCur==0}">
      <span>待处理</span>
    </div>
    <div class="tab-item" @click="setCur(1,'apply')" :class="{active: claimTabCur==1 }">
      <span>申请中</span>
    </div>
    <div class="tab-item" @click="setCur(2,'finished')" :class="{active: claimTabCur==2 }">
      <span>已完结</span>
    </div>
    <div class="tab-item" @click="setCur(3,'')" :class="{active: claimTabCur==3}">
      <span>全部</span>
    </div>
  </div>
</template>
<script>
import {
  mapGetters, mapMutations
} from 'vuex'

export default {
  computed: {
    ...mapGetters([
      'claimTabCur'
    ])
  },

  methods: {
    setCur (index, status) {
      this.SET_CLAIM_TAB_CUR(index)
      this.SET_CLAIM_TAB_CUR_STATE(status)
      this.$emit('changeTab')
    },
    ...mapMutations(['SET_CLAIM_TAB_CUR', 'SET_CLAIM_TAB_CUR_STATE'])

  }
}
</script>

<style lang="scss" scoped="scoped">
  @import "@/assets/styles/mixin.scss";
  .tabs {
    height: pxToRem(82px);
    line-height: pxToRem(82px);
    background: #FFFFFF;
    display: flex;
    justify-content: space-between;
    border-bottom: pxToRem(2px) solid #EDEDED;
    .tab-item {
      white-space: nowrap;
      width:25%;
      text-align: center;
      font-size: pxToRem(30px);
      color: $color-text;
      box-sizing: border-box;
      &.active {
        color: $color-text;
        border-bottom: pxToRem(4px) solid $color-active-text;
      }
    }
  }
</style>
