<template>
  <div>
    <div class="base-item"  @click='handleClickToDetail(claim)'>
      <div class="orderId ">
        <div class="orderSn ">
          理赔申请编号：{{claim.sn}}
        </div>
        <div class="orderStatus ">
          {{claim.status|claimStatus}}
        </div>
      </div>
      <div class="order-content">
        <div class="content-text">
          <div class="title-logo">
            <div class="title" >{{claim.refer_name}}</div>
            <div class="logo"><img :src='baseSrc+claim.brand_logo'></div>
          </div>
          <div class="msg">保单号: {{claim.order_policy_num}}</div>
          <div class="msg">被保险人: {{claim.relation_name}}</div>
          <div class="msg">保障利益:{{item_config.formula}} </div>
          <div class="msg">申请时间:{{claim.create_time}}  </div>
        </div>
      </div>

    </div>
    <div class="btn-contair"  v-if=" ![2,3].filter(i=>i==claim.status).length">
      <p class="btn btn-cancel" v-if=" [0,1,4].filter(i=>i===claim.status).length" @click="goCancel(claim)" >撤销</p>
      <p class="btn btn-deal" v-if=" [0,4].filter(i=>i===claim.status).length"  @click="goDeal(claim)">立即处理</p>
      <p class="btn btn-reapply" v-if=" [5,6].filter(i=>i===claim.status).length"  @click="goReapply(claim)">重新申请</p>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  props: {
    claim: {
      type: Object,
      default: () => {
        return {}
      }
    },
    item_config: {
      type: Object,
      default: () => {
        return {}
      }
    }

  },
  data () {
    return {
      baseSrc: ''
    }
  },
  mounted () {
    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
  },
  computed: {
    ...mapGetters(['userInfo'])
  },
  methods: {
    goCancel (item) {
      this.$emit('handleCancel', item.id)
    },
    goDeal (item) {
      this.$router.push({
        path: '/claimUpload',
        query: {
          claimApplySn: item.sn
        }
      })
    },
    goReapply (item) {
      /* type   0:个险理赔，1：团险理赔 */
      this.$router.push({
        path: '/claimList/claimApply',
        query: {
          orderSn: item.order_sn,
          type: parseInt(item.type)
        }
      })
    },
    handleClickToDetail (item) {
      this.$router.push({
        path: `/claimList/detail/${item.id}`,
        query: {
          claimId: item.id
        }
      })
    }
  }
}
</script>

<style lang="scss" >
  @import '@/assets/styles/mixin.scss';
  @import '@/assets/styles/baseList.scss';
  .btn-contair {
    background: #fff;
    height: pxToRem(100px);
    justify-content: flex-end;
    display: flex;
    .btn {
      width: pxToRem(150px);
      height: pxToRem(60px);
      line-height: pxToRem(60px);
      text-align: center;
      margin-top: pxToRem(20px);
      border-radius: pxToRem(30px);
      font-size: pxToRem(24px);
      background: #fff;
      margin-right: pxToRem(20px);
      &.last-child {
        margin-right: 0;
      }
      &.btn-cancel{
        color: #B6BBBB;
        border: pxToRem(2px) solid #D5D8D8;
      }
      &.btn-deal{
        color: #FF4B4B;
        border:pxToRem(2px) solid #FF4B4B;
      }
      &.btn-reapply{
        color:$color-active-text;
        border:pxToRem(2px) solid $color-active-text;
      }
    }

  }

</style>
