<template>
  <div>
    <claim-tab @changeTab="handleChangeTab"></claim-tab>
    <div class="list" v-if="claimTabCur===0">
      <div class="page-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <ul class="page-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-distance="50">
          <li v-for="(item,index) in dealList" class="page-infinite-listitem" :key="index">
            <base-claim :claim="item" :item_config="item.item_config" @handleCancel="showCancel"></base-claim>
          </li>
        </ul>
        <p v-show="loading" class="page-infinite-loading">
          <mt-spinner type="fading-circle"></mt-spinner>
          加载中...
        </p>
        <p v-show="!loading&&dealList.length" class="noMoreData">没有更多数据了</p>
        <div v-show="!dealList.length" class="no-result-wrapper">
          <no-result :title="'暂无理赔记录'" :src="src"
                     :absTop="82"></no-result>
        </div>
      </div>
    </div>
    <div class="list" v-if="claimTabCur===1">
      <div class="page-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <ul class="page-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-distance="50">
          <li v-for="(item,index) in applyList" class="page-infinite-listitem" :key="index">
            <base-claim :claim="item" :item_config="item.item_config" @handleCancel="showCancel"></base-claim>
          </li>
        </ul>
        <p v-show="loading" class="page-infinite-loading">
          <mt-spinner type="fading-circle"></mt-spinner>
          加载中...
        </p>
        <p v-show="!loading&&applyList.length" class="noMoreData">没有更多数据了</p>
        <div v-show="!applyList.length" class="no-result-wrapper">
          <no-result :title="'暂无理赔记录'" :src="src"
                     :absTop="82"></no-result>
        </div>
      </div>
    </div>
    <div class="list" v-if="claimTabCur===2">
      <div class="page-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <ul class="page-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-distance="50">
          <li v-for="(item,index) in finishedList" class="page-infinite-listitem" :key="index">
            <base-claim :claim="item" :item_config="item.item_config" @handleCancel="showCancel"></base-claim>
          </li>
        </ul>
        <p v-show="loading" class="page-infinite-loading">
          <mt-spinner type="fading-circle"></mt-spinner>
          加载中...
        </p>
        <p v-show="!loading&&finishedList.length" class="noMoreData">没有更多数据了</p>
        <div v-show="!finishedList.length" class="no-result-wrapper">
          <no-result :title="'暂无理赔记录'" :src="src"
                     :absTop="82"></no-result>
        </div>
      </div>
    </div>
    <div class="list" v-if="claimTabCur===3">
      <div class="page-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <ul class="page-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-distance="50">
          <li v-for="(item,index) in allList" class="page-infinite-listitem" :key="index">
            <base-claim :claim="item" :item_config="item.item_config" @handleCancel="showCancel"></base-claim>
          </li>
        </ul>
        <p v-show="loading" class="page-infinite-loading">
          <mt-spinner type="fading-circle"></mt-spinner>
          加载中...
        </p>
        <p v-show="!loading&&allList.length" class="noMoreData">没有更多数据了</p>
        <div v-show="!allList.length" class="no-result-wrapper">
          <no-result :title="'暂无理赔记录'" :src="src"
                     :absTop="82"></no-result>
        </div>
      </div>
    </div>
    <m-prompt :content="'确认撤销理赔申请？<br/>'+
'撤销后仍可再次申请理赔'" :btnLeftText="'取消'" :btnRightText="'撤销'"
              @handleBtnLeft="handleCancel"
              @handleBtnRight="handleRevoke()"
              v-show="cancelShow">

    </m-prompt>
    <router-view></router-view>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import noResult from '@/components/base/noResult'
import baseClaim from '@/components/claim/BaseClaim'
import claimTab from '@/components/claim/myClaimTab'
import mPrompt from '@/components/base/mPrompt'
export default {
  name: 'page-tab-container',
  data () {
    return {
      page: 1,
      limit: 10,
      dealList: [],
      applyList: [],
      finishedList: [],
      allList: [],
      src: require('@/assets/images/no_claim.png'),

      loading: false,
      allLoaded: false,
      wrapperHeight: 0,
      cancelShow: false,
      revokeId: ''
    }
  },
  mounted () {
    // this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top
    this.getList()
  },
  computed: {
    ...mapState([
      'claimTabCur',
      'claimTabCurState'
    ])

  },
  methods: {
    handleRevoke (id) {
      let postData = {
        id: this.revokeId
      }
      this.$api.claim.claimRevoke(postData).then(res => {
        if (res.code === 1) {
          // this.loading = true
          this.getList()
          this.cancelShow = false
        } else {
          this.$toast.center(res.message)
        }
      })
    },
    showCancel (id) {
      this.revokeId = id
      this.cancelShow = true
    },
    handleCancel () {
      this.cancelShow = false
    },
    handleChangeTab (index) {
      this.getList()
    },
    getList () {
      this.page = 1
      let postData = {
        page: this.page,
        limit: this.limit,
        status: this.claimTabCurState
      }
      this.$api.claim.getClaimList(postData).then((res) => {
        if (res.code === 1) {
          this.loading = true
          // this.lists = res.data.dataGrid.data
          switch (this.claimTabCurState) {
            case 'deal':
              this.dealList = res.data.data
              break
            case 'apply':
              this.applyList = res.data.data
              break
            case 'finished':
              this.finishedList = res.data.data
              break
            case '':
              this.allList = res.data.data
              break
          }

          this._checkMore(res.data)
          // $set(this.lists, `status${this.status}`, res.data.dataGrid.data)
        }
      })
    },
    loadMore () {
      if (!this.loading) {
        return
      }
      this.page++
      this.$api.claim.getClaimList({
        page: this.page,
        limit: this.limit,
        status: this.claimTabCurState
        // status: this.status
      }).then(
        function (res) {
          // this.$set(this.lists, `status${this.status}`, res.data.dataGrid.data)\
          if (res.code === 1) {
            switch (this.claimTabCurState) {
              case 'deal':
                this.dealList = this.dealList.concat(res.data.data)
                break
              case 'apply':
                this.applyList = this.applyList.concat(res.data.data)
                break
              case 'finished':
                this.finishedList = this.finishedList.concat(res.data.data)
                break
              case '':
                this.allList = this.allList.concat(res.data.data)
                break
            }
          }
          this._checkMore(res.data)
        }.bind(this)
      )
        .catch(function (err) {
        })
    },
    _checkMore (data) {
      if (!data.data.length || (this.page * this.limit) >= data.count) {
        this.loading = false
      }
    }
  },

  components: {
    claimTab,
    baseClaim,
    noResult,
    mPrompt
  }
}
</script>

<style lang="scss" scoped>
  @import '@/assets/styles/mixin.scss';
  .noMoreData{
    margin-top: pxToRem(30px);
    color: #A5A8A8;
    text-align: center;
  }
  .page-infinite-listitem {
    margin-bottom: pxToRem(20px);
  }
</style>
